import React from 'react';
import ReactDOM from 'react-dom';
import './index.css'

import { Provider } from 'react-redux';

import { Store } from './redux/store/index'

import { App } from './App';

ReactDOM.render(
  <React.StrictMode>
    <Provider store={Store}>
      <App />
    </Provider>
  </React.StrictMode>,
  document.getElementById('root')
);
